import React, { useState } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";

const DateClick = () => {
  // State to manage events
  const [events, setEvents] = useState([]);

  // Function to handle date clicks and add an event
  const handleClick = (info) => {
    console.log("clicked");
    const title = prompt("Enter event title:");
    if (title) {
      const newEvent = { id: Date.now(), title, start: info.dateStr }; // Ensure each event has a unique ID
      setEvents([...events, newEvent]); // Add new event to the state
    }
  };

  // Function to handle event drag-and-drop
  const handleEventDrop = (info) => {
    const updatedEvents = events.map((event) =>
      event.id === info.event.id
        ? { ...event, start: info.event.startStr }
        : event
    );
    setEvents(updatedEvents);
  };

  // Function to handle event resize
  const handleEventResize = (info) => {
    const updatedEvents = events.map((event) =>
      event.id === info.event.id
        ? { ...event, start: info.event.startStr, end: info.event.endStr }
        : event
    );
    setEvents(updatedEvents);
  };

  return (
    <div className="p-4">
      <h2>React FullCalendar - Editable Events</h2>
      <FullCalendar
        plugins={[dayGridPlugin, interactionPlugin]}
        initialView="dayGridMonth"
        dateClick={handleClick}
        events={events}
        editable={true} // Enables drag and drop
        eventDrop={handleEventDrop} // Handles event move
        eventResize={handleEventResize} // Handles event resize
        headerToolbar={{
          left: "prev,next today",
          center: "title",
          right: "dayGridMonth,dayGridWeek,dayGridDay",
        }}
      />
    </div>
  );
};

export default DateClick;
